<style lang="less">
</style>
<template>
  <a-form :form="form" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" @submit="handleSubmit">
    <a-form-item label="旧密码">
      <a-input-password
          v-decorator="['oldPwd', { rules: [{ required: true, message: '请输入你的旧密码' }] }]"
      />
    </a-form-item>
    <a-form-item label="新密码">
      <a-input-password
          v-decorator="['newPwd', { rules: [{ required: true, message: '请输入你的新密码' }] }]"
      />
    </a-form-item>
    <a-form-item label="确认新密码">
      <a-input-password
          v-decorator="['newPwdAgain', { rules: [{ required: true, message: '请再输入你的新密码' }] }]"
      />
    </a-form-item>
    <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
      <a-button type="primary" html-type="submit">
        Submit
      </a-button>
    </a-form-item>
  </a-form>
</template>

<script>
import {JeecgListMixin} from "@/mixins/JeecgListMixin"
import {getUserDepts, changePwdUrl} from "@/api/laisonorder";

export default {
  name: "pwd-form",
  mixins: [JeecgListMixin],
  components: {},
  props: {
    userInfo: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      depts: "",
      form: this.$form.createForm(this, {name: "coordinated"})
    }
  },
  created() {

  },
  mounted() {
    this.getDepartment()
  },
  watch: {
    userInfo: function (newval, oldval) {
    }
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          values.oldPwd = window.btoa(values.oldPwd)
          values.newPwd = window.btoa(values.newPwd)
          values.newPwdAgain = window.btoa(values.newPwdAgain)
          changePwdUrl(values).then(res => {
            //执行退出
            this.$emit("changeSuccess")
          }).catch(err => {
            this.$message.error(err.errmsg)
          })
        }
      });
    },

    getRole() {
      if (this.userInfo) {
        let roles = this.userInfo.roles
        if (roles) {
          let res = ""
          for (let i = 0; i < roles.length; i++) {
            res += roles[i].name
            if (i < roles.length - 1) {
              res += ","
            }
          }
          return res
        }
      }
      return "未知"
    },
    getDepartment() {
      getUserDepts().then((res) => {
        let depts = res.result
        if (depts) {
          let result = ""
          for (let i = 0; i < depts.length; i++) {
            result += depts[i].name
            if (i < depts.length - 1) {
              result += ","
            }
          }
          this.depts = result
        }
      }).catch((err) => {

      })
    },
    loginout() {
      this.$emit("loginout")
    }
  }
}
</script>

<style lang='less' scoped>

.avatar {
  margin: 0 auto;
  width: 50%;
  height: 40%;
  margin-left: 25%;
  margin-top: 10px;
  overflow: hidden;
}

.btn_login_out {
  position: relative;
  margin-top: 30px;
  margin-left: 12%;
  margin-right: 12%;
  width: 76%;
  background-color: #2D8CF0;
  height: 40px;
  font-size: 16px;
  color: white;
  border-radius: 20px;
}

.account-center-detail {
  width: 100%;


  p {
    margin-bottom: 8px;
    padding-left: 15px;
    position: relative;
  }

  i {
    position: absolute;
    height: 14px;
    width: 14px;
    left: 0;
    top: 4px;
    background: url(https://gw.alipayobjects.com/zos/rmsportal/pBjWzVAHnOOtAUvZmZfy.svg)
  }

  .title {
    background-position: 0 0;
  }

  .group {
    background-position: 0 -22px;
  }

  .address {
    background-position: 0 -44px;
  }
}

.account-center-tags {
  .ant-tag {
    margin-bottom: 8px;
  }
}

.account-center-team {

  .members {
    a {
      display: block;
      margin: 12px 0;
      line-height: 24px;
      height: 24px;

      .member {
        font-size: 14px;
        color: rgba(0, 0, 0, .65);
        line-height: 24px;
        max-width: 100px;
        vertical-align: top;
        margin-left: 12px;
        transition: all 0.3s;
        display: inline-block;
      }

      &:hover {
        span {
          color: #1890ff;
        }
      }
    }
  }
}

.tagsTitle, .teamTitle {
  font-weight: 500;
  color: rgba(0, 0, 0, .85);
  margin-bottom: 12px;
}

.ant-upload-preview {
  position: relative;
  margin: 0 auto;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  box-shadow: 0 0 4px #ccc;

  .upload-icon {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 1.4rem;
    padding: 0.5rem;
    background: rgba(222, 221, 221, 0.7);
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.2);
  }

  .mask {
    opacity: 0;
    position: absolute;
    background: rgba(0, 0, 0, 0.4);
    cursor: pointer;
    transition: opacity 0.4s;

    &:hover {
      opacity: 1;
    }

    i {
      font-size: 2rem;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -1rem;
      margin-top: -1rem;
      color: #d6d6d6;
    }
  }

  img, .mask {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
  }
}
</style>

